#root {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

#app_wrapper {
    display: -ms-flexbox;
    display: flex;
}

/*Uncomment the style for the board to play chess in a way more visually challenging way*/
/*#board {*/
    /*display: flex;*/
    /*margin: 0 auto;*/
/*}*/

.row div {
    vertical-align: top;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

/* squares */
.square {
    width: 64px;
    height: 64px;
    -webkit-transition: border 0.25s;
    -o-transition: border 0.25s;
    transition: border 0.25s;
    /* so that the piece doesn't move on select */
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 2px transparent solid;
    background-position: left -1px center;
}

.square.dark {
    background-color: saddlebrown;
}

.square.light {
    background-color: wheat;
}

.highlighted {
    border: 2px cornflowerblue solid;
}

.highlighted2 {

}

.selected {
    border: 2px mediumseagreen solid;
}

/* right panel */
#right_panel {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 250px;
}

#right_panel > div {
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 10px;
}

#move_history_panel {
    background-color: whitesmoke;
}

#captured_pieces_panel {
    background-color: cornflowerblue;
}


